footer {
    background-color: #2e2d2d;
    width: 100%;
    padding: 2rem 0;
}

.section-info{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 80%;
    margin: 0 auto;
}
@media screen and (max-width: 480px){
    .section-info{
        width: 90%;
    }
} 

.logo-row{
    position: relative;
    height: 75px;
    width: 100%;
}

.footer-column {
    min-width: 250px;
    display: flex;
    flex-direction: column;
    width: calc(100%/3);
    padding: 1rem;
    flex-shrink: 1;
}
@media screen and (max-width: 480px){
    .footer-column{
        width: 100%;
        padding: 1rem 1rem 1rem 0;
    }
} 

.left-column {
    gap: 1rem;
}

.middle-column{
    align-items: center;
}
@media screen and (max-width: 480px){
    .middle-column{
        align-items: start;
    }
} 

.right-column {
    align-items: end;
}
@media screen and (max-width: 480px){
    .right-column{
        align-items: start;
    }
} 

.services-group {
    width: fit-content;
}

.footer-subgroup {
    display: flex;
    gap: .5rem;
}

.dev-mail {
    color: #c0bdbd;
}
.dev-mail:hover {
    scale: 1;
    color: white;
    text-decoration: underline;
}

.social-icons-row {
    display: flex;
    gap: .5rem;
    justify-content: end;
}
@media screen and (max-width: 480px){
    .social-icons-row{
        justify-content: start;
    }
} 


.footer-column:nth-child(2){
    flex-shrink: 2.5;
}

.subtitles-footer{
    color: #6d6b6b;
}

.text-footer {
    color: #c0bdbd;
    margin: 0 0 .3rem 0;
}

.copyrights-text{
    color: #6d6b6b;
    font-weight: 400; 
    margin: 0 0 .3rem 0;
}

.logo-footer{
    position: absolute;
    top:0;
    left: 0;
    scale: 0.3;
}

hr {
    width: 75%;
    margin: 2rem auto;
}
@media screen and (max-width: 480px){
    hr {
        width: 90%;
    }
} 

.form-footer{
    width: 100%;
    display: flex;
    gap: 5%;
}

.input-footer {
    height: 3rem;
    width: 70%;
    padding: 1rem;
    color: #2e2d2d;
}
@media screen and (max-width: 480px) {
    .input-footer{
        width: 100%;
    }
}
.input-footer:focus{
    border: none;
    outline: solid 3px #e2a694ec;
    border-radius: .1rem;
}

.send-btn-footer{
    width: 110px;
    height: 3rem;
    background-color: #2e2d2d;
    border: solid 2.5px #ebe8e8;
    color: #ebe8e8;
    font-weight: bold;
    font-size: 1.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .3rem;
}
.send-btn-footer:hover{
    color: #e2a694;
    cursor: pointer;
}
.send-btn-footer:active{
    scale: 0.9;
}

.section-copyright{
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    padding: 1rem;
}
@media screen and (max-width: 480px){
    .section-copyright {
        width: 100%;
    }
} 

.dev{
    font-family: 'Delicious Handrawn', cursive;
    font-size: 1.8rem;
    color: #e2a694ec;
}

@media screen and (max-width: 480px){
    .copyrights-text {
        text-align: center;
        margin-bottom: 1.5rem;
    }
} 