* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
}

:root{
  /* COLORS */
  --bg-light: #FEFFFF;
  --bg-dark: #282A36; 
  --color-primary: pink;
  --text-dark-primary: #2B2B2B;
  --text-dark-secundary: #363636;
  /* TEXT-STYLE */
  --ts-display: calc(((1rem * 1.618) * 1.618) * 1.618); 
  --ts-header: calc((1rem * 1.618) * 1.618); 
  --ts-title: calc(1rem * 1.618); 
  --ts-body: 1rem; 
  --ts-caption: 0.75rem;
}

html {
  background-color: #ECf0F3;
}
