.window-wrapper {
    background-color: grey;
    max-width: 300px;
    max-height: 250px;
    border-radius: 1rem;
    opacity: 70%;
    
}
.window-wrapper:hover{
    box-shadow: 0 0 11px rgba(33,33,33,.2); 
    opacity: 100%;
} 

.window-menu{
    width: 100%;
    height: fit-content;
    background-color: #424141;
    display: flex;
    padding: 0.5rem 1rem;
    border-radius: 1rem 1rem 0 0;
}
.window-img{
    height: 100%;
    width: 100%;
}
img {
    height: inherit;
    width: inherit;
    object-fit: contain;
    border-radius: 0 0 1rem 1rem;
}