/* FORM STYLE */

.contact-form {
    margin: 0 auto;
    width: 50%;
    display: flex;
    flex-direction: column;
}
@media screen and (max-width: 1024px) {
    .contact-form  {
        min-width: 70%
    }
}
@media screen and (max-width: 780px) {
    .contact-form  {
        min-width: 80%
    }
}
@media screen and (max-width: 480px) {
    .contact-form  {
        min-width: 100%
    }
}

/* FORM GROUPS */

.contact-form__group {
    display: flex;
    flex-direction: column;
    width: 50%;
    gap: 4px;
    margin-bottom: 1rem;
}

@media screen and (max-width: 480px) {
    .contact-form__group  {
        width: 100%
    }
}

.contact-form__group > input {
    padding: .7rem 1.5rem;
    /* background-color: #5755553a; */
    background-color: #ECf0f3;
    box-shadow: -3px -3px 7px #ffffff,
    3px 3px 5px #ceced1;
    border: none;
    border-radius: 4px;
    outline: none;
}

.contact-form__group--text-area{
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 4px;  
}

.contact-form__group--text-area textarea {
    resize: none;
    padding: .7rem 1.5rem;
    background-color: #ECf0f3;
    box-shadow: -3px -3px 7px #ffffff,
    3px 3px 5px #ceced1;
    border: none;
    border-radius: 4px;
    outline: none;
}

/* FORM SUBMIT BUTTON */
.contact-form__group--button {
    display: flex;
    width: 100%;
}

@media screen and (max-width: 480px) {
    .contact-form__group--button > button  {
        width: 100%
    }
}

.dark-button{
    all: unset;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
    text-align: center;
    margin-top: 2.5rem;
    background-color: #333232;
    color: #f1efef;
    width: 50%;
    padding: .7rem 1.5rem;
    border-radius: 4px;
    box-shadow: -3px -3px 7px #ffffff,
    3px 3px 5px #bdbdc0;
}

.green-button {
    all: unset;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
    text-align: center;
    margin-top: 2.5rem;
    background-color: #4CBA6F;
    color: #f1efef;
    width: 40%;
    padding: .7rem 1.5rem;
    border-radius: 4px;
    box-shadow: 15px 15px 30px #d1d9e6;
}

.dark-button:hover{
    background-color: #444343;
    cursor: pointer;
}

.dark-button:active{
    transform: translateY(2px);
}

/* WARNING INPUTS MESSAGE */

.warning-message{
    color: red;
    margin-top: 2rem;
    text-align: center;
}

.invalidInputMessage{
    color: red;
    font-size: .8rem; 
    margin-top: .2rem;  
}

.submit-warning {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: red;
}
.submit-warning > p {
    text-align: center;
    font-size: .8rem;
}

