.main-wrapper {
    width: 80%;
    margin: 0 auto;
    min-height: 80vh;
    display: flex;
    align-items: center;
    gap: 50px;
    justify-content: center;
}

@media screen and (max-width:768px){
    .main-wrapper{
        flex-direction: column;
        gap: 0;
    }
    .img-column{
        order: 1;
    }
    .content-column{
        order: 2;
        margin-bottom: 50px;
    }
}

@media screen and (max-width:480px){
    .main-wrapper{
        padding-top: 70px;
        width: 90%;
    }
}

.title, h2 {
    color: #000000d8;
}

.subtitle {
    color: #000000a1;
}

.content-column h1 {
    font-size: 4rem;
}

.img-column {
    max-width: 350px;
}

.img-column > img {
    padding: 2.5px;
    border-radius: 50%;
    box-shadow: -3px -3px 7px #ffffff,
    3px 3px 5px #ceced1;
}
.portrait{
    width: 100%;
    margin: 50px 0;
}

h1 > span {
    color: hotpink;
}

/* ------------------------------------------ */

.title-banner{
    width: 80%;
    margin: 25px auto;
}
@media screen and (max-width:480px){
    .title-banner{
        width: 90%;
        margin: 25px auto;
    }
}
.title-banner > h2 {
    font-size: 3rem;
}

.portfolio-section{
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    margin: 0 auto;
    padding: 5rem 0;
    align-items: center;
    justify-content: center;
}
@media screen and (max-width:480px){
    .portfolio-section{
        width: 90%;
    }
}

.portfolio-section > h3 {
    margin: 50px 0 25px 0;
}

.about-section{
    width: 80%;
    margin: 0 auto;
    padding: 2rem 0 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5rem;
}
@media screen and (max-width:1024px){
    .about-section{
        flex-direction: column;
    }
}
@media screen and (max-width:480px){
    .about-section{
        width: 90%;
    }
}

.bold-text{
    font-weight: bold;
}

.biography-card{
    padding: 4rem 2.5rem;
    border-radius: 1rem;
    max-width: 400px;
    border: none;
    box-shadow: inset 3px 3px 5px #ceced1,
    inset -3px -3px 7px #ffffff;
    
}
.biography-card:hover{
    box-shadow: 0 0 11px #21212133; 
}

.biography-title{
    font-size: 2.5rem;
    margin-bottom: 1rem;
}

.biography-subtitle{
    font-size: 1.5rem;
    margin: 1rem 0 .1rem 0;
}

.biography-card > p {
    line-height: 2.2rem;
    letter-spacing: 0.1rem;
    margin-bottom: .75rem;
}

.tools-card{
    background-color: #21242cf6;
    padding: 2rem;
    border-radius: 1rem;
    max-width: 72c0px;
    border: 1px solid var(--bg-dark);
}
.tools-card > p {
    line-height: 2rem;
    letter-spacing: 0.1rem;
    color: #AFAFB4;
}
.tools-card:hover{
    box-shadow: 0 0 11px rgba(33,33,33,.2); 
}

.color-pink{
    color: #a14f7f;
}
.color-blue{
    color: #29779b;
}
.color-grey{
    color: #AFAFB4
}
.color-green{
    color: #3aa861
}
 
.ml-1 {
    margin-left: 1rem;
}

.ml-2{
    margin-left: 2rem;
}

.contact-section {
    width: 80%;
    margin: 0 auto;
    margin-bottom: 10rem;
    display: flex;
    flex-direction: column;
    gap: 2.5rem;
}
@media screen and (max-width:480px){
    .contact-section{
        width: 90%;
        margin-bottom: 5rem;
    }
}


