.nav{
    height: 70px;
    width: 100vw;
    background-color: #ECf0f3;
    color: #282c34;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    box-shadow: -3px -3px 7px #ffffff,
    3px 3px 5px #bdbdc0;
    z-index: 10;
  }

.nav > .nav-ul {
  width: 50%;
  display: flex;
  list-style-type: none;
  align-items: center;
  justify-content: flex-end;
  gap: 35px;
  margin-right: 100px;
}

.nav-ul li {
  display: flex;
}

a{
  text-decoration: none;
  color: #000000d5;
  text-shadow:  1px 1px 1px #00000031;
  ;
}

a:hover{
  color:#1d1c1c;
  scale: 1.05;
}
  
/* .menu-h{
  display: none;
} */

.menu-h-hidden{
  display: none;
}

.menu-h-visible {
  position: absolute;
  top: 70px;
  left: 0;
}
  
@media screen and (max-width: 720px) {
  .menu-h{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: #000000ec;
    width: 100%;
    height: calc(100vh - 70px);
    text-align: center;
  }

  .menu-h > li > a {
    color: white;
  }
}
  
@media screen and (max-width: 720px) {
  .nav > .nav-ul {
    display: none;
    /* opacity: 0; */
  }
}

.logo-link {
  display: flex;
  width: 220px;
  align-items: center;
  padding-left: 2.5rem;

}

.brand {
  height: 50px;
}

.toggler-btn {
  color: black;
  margin-right: 25px;
  /* background: none;
  border: none; */
  display: none;
}

.toggler-btn:hover {
  color: #272626;
  cursor: pointer;
}

@media screen and (max-width: 720px) {
  .toggler-btn {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

/* CATEGORIES DROPDOWN MENU */

.categories-menu{
  position: relative;
  cursor: pointer;
  display: flex;
  flex-direction: row;
}
.categories-menu:hover{
  color: #4b4949;

}
.categories-dropdown{
  display: none;
  visibility: hidden;
}

.categories-menu:hover .categories-dropdown{
  display: flex;
  visibility: visible;
  flex-direction: column;
  gap: 5px;
  position: absolute;
  top: 25px;
  left:0;
  background-color: #fffffff8;
  width: 100%;
  padding: .3rem;
  border-radius: 0 0 5px 5px;
}

.categories-dropdown > li {
  width: 100%;
}

.categories-dropdown__hidden{
  display: none;
}

.categories-dropdown__inactive{
  margin-top: .3rem;
  display: flex;
  flex-direction: column;
  gap: .3rem;
}

.categories-dropdown__inactive > li {
  list-style-type: none;
}

@media screen and (max-width: 720px) {
  .categories-menu{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .categories-menu > svg {
    display: none;
  }
}

/* NAV MENU DROPDOWN */

.menu-dropdown__active{
  padding: .5rem 1rem 1rem 1rem;
  z-index: 100;
  display: block;
  position: absolute;
  top: 70px;
  left: 0;
  width: 100vw;
  background-color: #ffffffd8;
}

@media screen and (min-width: 720px) {
  .menu-dropdown__active{
    display: none;
  }
}

.menu-dropdown__active > li {
  padding: .5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu-dropdown__active > li > a{
  width: 100%;
  text-align: center;
}

.menu-dropdown__active > li > a > button{
  margin: 0 auto;
}
.menu-dropdown__inactive{
  display: none;
}

/* LANGUAGE DROPDOWN */

.nav-dropdown{
  position: relative;
  display: flex;
  cursor: pointer;
}

.nav-dropdown:hover .nav-dropdown-menu{
  visibility: visible;
  opacity: 1;
}

.nav-dropdown-menu {
  /* display: none; */
  display: flex;
  flex-direction: column;
  gap: .3rem;
  visibility: hidden;
  opacity: 0;
  background-color: #1d1c1c;
  position: absolute;
  top: 30px;
  left: 0;
  width: 120px;
  padding: .5rem 1rem;
  border-radius: 5px;
  background-color: #ECf0f3;
  box-shadow: -3px -3px 7px #ffffff,
  3px 3px 5px #ceced1;
  z-index: 2;
  transition: 300ms;
}

.nav-dropdown-menu::before{
  content: "";
  position: absolute;
  top: -3px;
  right: 4px;
  background-color: #1d1c1c;
  width: 15px;
  height: 15px;
  transform: rotate(45deg);
  background-color: #ECf0f3;
  box-shadow: -3px -3px 7px #ffffff;
  z-index: 1;
}

.nav-dropdown-menu > li {
  display: flex;
  width: 100%;
  align-items: center;
  gap: .3rem;
}

.nav-dropdown-menu > li:hover{
  color:#5e5b5b;
}

.nav-dropdown-menu > li > img {
  z-index: 5;
  height: 1rem;
}